import { useTranslation } from "react-i18next";
import styled from 'styled-components';
import FreezingSVG from '../../img/period/Freezing.svg'
import InvestSVG from '../../img/period/Invest.svg'
import ManagementSVG from '../../img/period/Management.svg'
import RedemptionSVG from '../../img/period/Redemption.svg'
import Colon from '../../img/period/colon.svg'

