const CardPanel = styled.span`
    display: inline-block;
    width: 4rem;
    height: 4.5rem;
    background: rgba(255, 91, 91, 0.16);
    border-radius: .6rem;
    border: 1px solid #FF5B5B;
    font-size: 3.2rem;
    font-weight: 500;
    color: #FF5B5B;
    line-height: 4.5rem;
    margin: 0 6px;
    margin-top: 2rem;
`


const CountDown = React.memo(props => {
    const { t } = useTranslation();

    const [targetTime, setTargetTime] = useState(0)
    const [leftTime, setLeftTime] = useState(0)
    const TargetRef = useRef(leftTime);

    const generateCard = useCallback(
        (num) => {
            const numstr = `${num < 10 ? '0' : ''}${num}`.split('')
            return numstr.map((s, i) => <CardPanel key={i}>{s}</CardPanel>)
        },
        []
    )
    useEffect(() => {
        TargetRef.current = targetTime;
    });

    useEffect(
        () => {
            const timer = setInterval(() => {
                const currentTime = parseInt(Date.now() / 1000)
                const delta = TargetRef.current - currentTime
                if (delta === 0) {
                    props.update && props.update()
                }
                if (delta < 0) return
                setLeftTime(delta)
            }, 1000);
            return () => clearInterval(timer)
        },
        []
    )
    useEffect(
        () => {
            setTargetTime(props.countdown)
        },
        [props.countdown]
    )
    const LeftTimeDisplay = useMemo(
        () => {
            const days = parseInt(leftTime / 86400)
            let left = leftTime % 86400
            const hours = parseInt(left / 3600)
            left = left % 3600
            const minutes = parseInt(left / 60)
            const seconds = left % 60

            return {
                days: generateCard(days),
                hours: generateCard(hours),
                minutes: generateCard(minutes),
                seconds: generateCard(seconds),
            }
        },
        [leftTime]
    )

    return <Period className='raising'>
        <div className='period-container'>
            <div className='text'>{t('venture.Remaining')}</div>
            <div>
                {LeftTimeDisplay.days}
                <span className='days'>{t('venture.Days')}</span>
                {LeftTimeDisplay.hours}
                <img className='colon' src={Colon} alt="" />
                {LeftTimeDisplay.minutes}
                <img className='colon' src={Colon} alt="" />
                {LeftTimeDisplay.seconds}
            </div>
            <div className='btn-group'>
                {props.children}
            </div>
        </div>
    </Period>
})

const VenturePeriod = React.memo(props => {
    const { t } = useTranslation();

    const VenturePeriod = useMemo(
        () => {
            return [
                {
                    name: "",
                    img: "",
                },
                {
                    name: "venture.GPInvest",
                    img: "",
                },
                {
                    name: "venture.LPInvest",
                    img: "",
                },
                {
                    name: "venture.RaiseFail",
                    img: ManagementSVG,
                },
                {
                    name: "venture.FreezingPeriod",
                    img: FreezingSVG,
                },
                {
                    name: "venture.Settlement",
                    img: RedemptionSVG,
                },
            ]
        },
        []
    )
    const currentPeriod = useMemo(
        () => {
            return VenturePeriod[props.period] || {}
        },
        [props.period]
    )
    const period = props.period
    if (period === 1 || period === 2) {
        return <CountDown update={props.update} countdown={props.countdown}>{props.children}</CountDown>
    }
    return <Period>
        <div className='period-container'>
            <img className="icon" src={currentPeriod.img} alt="" />
            <span className="title" >{t(currentPeriod.name)}</span>
            <div className='btn-group'>
                {props.children}
            </div>
        </div>
    </Period>
})

export default VenturePeriod