const YourInvest = ({mintToken, deposit})=>{
    const { t } = useTranslation();
    const [transferVisible, setTransferVisible] = useState(false)

    return (
        <Block className='no-style'>
            <TransferToken token={mintToken} show={transferVisible} handleClose={()=>{setTransferVisible(false)}} />
            <div className="titleTop">{t('venture.YourShare')}</div>
            <Ul className='font-light'>
                <LI>
                    <div className='title'>Your Token</div>
                    <div className='value1 box'>
                        {mintToken.userBalanceDisplay}
                        <TansferImg src={accounts} alt="" onClick={()=>{setTransferVisible(true)}} />
                    </div>
                    <div className='symbol'>{mintToken.symbol}</div>
                </LI>
                <LI>
                    <div className='title'>Your Deposit</div>
                    <div className='value1'>{mintToken.userBalanceDisplay}</div>
                    <div className='symbol'>{deposit.symbol}</div>
                </LI>
            </Ul>
        </Block>
    )
}

