const PercentUI = React.memo(props => {
    return (
        <PercentUIdiv>
            <div className='pipe'>
                <div className='progress' style={{ width: props.val }}></div>
            </div>
            <span>{ props.val }</span>
        </PercentUIdiv>
    )
})


export default function Venture(props) {
    let { t } = useTranslation();

    const { state, dispatch } = useWeb3();
    const { Account, venturecontract, maincontract, whoAmI, web3Instance, votecontract, stockcontract, daoContractAddress} = state;

    const [id, setAId] = useState(null);

    const [isMember, setisMember] = useState(false);
    const [isModerator, setisModerator] = useState(false);
    const [isOwner, setisOwner] = useState(false);

    const [errorShow, seterrorShow] = useState(false);
    const [errorTips, seterrorTips] = useState('');

    const [buyShow, setBuyShow] = useState(false);
    const [raiseGp, setRaiseGp] = useState(false);
    const [raiseLp, setRaiseLp] = useState(false);
    const [deposit, setDeposit] = useState(false);
    const [withdraw, setWithdraw] = useState(false);
    const [confirmShow, setConfirmShow] = useState(false);
    const [confirmTips, setConfirmTips] = useState('');
    const [tokenList, setTokenList] = useState([])

    const [period, setPeriod] = useState()

    const [mintToken, setMintToken] = useState({})
    const [fund, setFund] = useState({})

    const [yourDeposit, setYourDeposit] = useState({})
    const [manageFee, setManageFee] = useState(0)
    const [bonus, setBonus] = useState(0)
    const [investTime, setInvestTime] = useState({})

    const [tokenInfo, setTokenInfo] = useState({
        contract: '',
        id: ''
    })

    const VenturePeriod = [
        "",
        `${t('venture.GPInvest')}`,
        `${t('venture.LPInvest')}`,
        `${t('venture.RaiseFail')}`,
        `${t('venture.FreezingPeriod')}`,
        `${t('venture.Settlement')}`
    ]

    useEffect(() => {
        setAId(props.match.params.id);
    }, [props.match.params.id]);

    useEffect(async () => {
        if(maincontract == null) dispatch({ type: 'LOAD_MAINCONTRACT' });
    }, []);

    useEffect(async()=>{
        if (!id || !mainConnect) return
    }, [id, mainConnect])


    useEffect(() => {
        if (whoAmI ==null) return;
        setisOwner(whoAmI[0]);
        setisModerator(whoAmI[1]);
        setisMember(whoAmI[2]);
    }, [id, maincontract, whoAmI]);

    useEffect(()=>{
        if (!venturecontract) return
    }, [venturecontract])


    const handleInput = (e) => {
        const { name } = e.target;
        tokenInfo[name] = e.target.value
        setTokenInfo(tokenInfo)
    }

    const clickSearch = ()=>{

    }
    const clickRaise = async ()=>{
        if (period !== 1) return
        const gpStopHeight = parseInt((await venturecontract.GpSubStopTime()).toString())
        const currentHeight = Math.round(Date.now() / 1000)
        console.log(`gpStopTime: ${gpStopHeight}, currentTime: ${currentHeight}`)
        if (currentHeight >= gpStopHeight) {
            // config LP raised
            setRaiseLp(true)
        } else {
            dispatch({ type: 'MSGTYPE', payload: { msg: `${t('venture.notReached')}`, closable: true } });
        }
    }
    const closeGPRaise = ()=>{
        setRaiseGp(false)
    }
    const closeLPRaise = ()=>{
        setRaiseLp(false)
    }
    const clickDeposit = ()=>{
        setDeposit(true);
    }

    const closeDeposit = ()=>{
        setDeposit(false)
    }
    const clickWithdraw = ()=>{
        setWithdraw(true);
    }
    const clickTokenBuyModal = ()=>{
        
    }
    const closeTokenBuyModal = ()=>{
        
    }

    const closeWithdraw = ()=>{
        setWithdraw(false)
    }
    const clickCloseVenture = ()=>{
        setConfirmShow(true);
        setConfirmTips(1);
    }
    const clickFreeze = ()=>{
        setConfirmShow(true);
        setConfirmTips(3);

    }
    const CloseShow = ()=>{
        setConfirmShow(false);
    }

    const clickRefund = ()=>{
        setConfirmShow(true);
        setConfirmTips(2);
    }

    const getVenturePeriod = async()=>{
        try {
            const period = await venturecontract.getVenturePeriod()
            console.log("[current period]", period, VenturePeriod[period])
            setPeriod(period)
        } catch (error) {
            console.error('getVenturePeriod failed:', error)
        }
    }
    useEffect(()=>{
        if (!venturecontract) return
        getVenturePeriod()
    }, [venturecontract])
    
    const getfund = async()=>{
        try {
            const {raised, goal, address, hard} = await api.venture.getFundInfo(venturecontract)
            const fundContract = await api.erc20.getContract(web3Instance, address)
            const symbol = await fundContract.symbol()
            const deci = await fundContract.decimals()
            const f = {
                raised: ethers.utils.formatUnits(raised, deci), 
                goal: ethers.utils.formatUnits(goal, deci),
                hard: ethers.utils.formatUnits(hard, deci),
                symbol, address,deci
            }
            f.raisedDisplay = getShortDisplay(f.raised)
            f.goalDisplay = getShortDisplay(f.goal)
            setFund(f)
        } catch (error) {
            console.error("getfund failed", error)
        }
    }
    const getStockToken = async()=>{
        const deci = await stockcontract.decimals()
        let mintToken = {
            deci: deci,
            address: daoContractAddress['VenturesStockManager'],
            symbol: await stockcontract.symbol(),
            balance: ethers.utils.formatUnits(await stockcontract.totalSupply(), deci),
            userBalance: ethers.utils.formatUnits(await stockcontract.balanceOf(Account), deci)
        }
        mintToken.userBalanceDisplay = getShortDisplay( mintToken.userBalance)
        mintToken.balanceDisplay = getShortDisplay( mintToken.balance)
        setMintToken(mintToken)
    }

    const refreshData = ()=>{
        getfund()
        getStockToken()
    }

    useEffect(()=>{
        if (!venturecontract) return
        getfund()
    }, [venturecontract, period])

    useEffect(()=>{
        if (!venturecontract || !stockcontract) return
        getStockToken()
    }, [daoContractAddress, stockcontract, period])

    useEffect(()=>{
        if (!venturecontract) return 
        const getOthers = async()=>{
            try {
                const fee = await venturecontract.ManageFeePercent()
                setManageFee((fee.toNumber()/100).toFixed(2))
            } catch (error) {
                console.error('get ManageFeePercent failed', error)
            }
            try {
                const bonus = await venturecontract.totalGPBonusPercent()
                setBonus((bonus.toNumber()/100).toFixed(2))
            } catch (error) {
                console.error('get totalGPBonusPercent failed', error)
            }
        }
        getOthers()
    }, [venturecontract])

    const getInvestTime = async()=>{
        const lpTime = await venturecontract.LpSubStopTime()
        const gpTime = await venturecontract.GpSubStopTime()
        const currentTime = Math.round(Date.now() / 1000)
        console.log('lpTime:', lpTime.toNumber(), 'gpTime: ', gpTime.toNumber(), 'currentTime: ', currentTime)
        setInvestTime({
            lpTime: lpTime.toNumber(), 
            gpTime: gpTime.toNumber(), 
            currentTime: currentTime
        })
    }

    useEffect(()=>{
        if (venturecontract) getInvestTime()
    }, [venturecontract, period])

    useEffect(()=>{
        if (!fund || !fund.symbol || mintToken.userBalance) return
        const getYourDeposit = async()=>{
            try {
                // TODO
                setYourDeposit({
                    // value: mintToken.userBalance,
                    symbol: fund.symbol
                })
            } catch (error) {
                
            }
        }
        getYourDeposit()
    }, [fund, mintToken])

    useEffect(()=>{
        const getStatus = async()=>{
            const s = await votecontract.isOpenVote()
            console.log("****isOpenVote****", s)
        }
        if (votecontract) getStatus()
    }, [votecontract])

    const updateToken = useCallback(
        (tokenlist) => {
            setTokenList(tokenlist)
        },
        []
    )

 

    const updateDaoID = useCallback(
        id=>setAId(id),
        []
    )

    return (
        <div>
            <Modal
                visible={errorShow}
                onCancel={() => seterrorShow(false)}
                footer={null}
            >
                <Tip>{errorTips}</Tip>
            </Modal>


            <div className='container'>
                <Left history={props.history} handle={props.match.params.handle} updateDaoID={updateDaoID} updateToken={updateToken} />
                <Section>
                    {
                        period && <VenturePeriodPanel
                            period={period}
                            update={getInvestTime}
                            countdown={ period === 1 ? investTime.gpTime : (period === 2 ? investTime.lpTime : 0)}
                        >
                            <>
                                {
                                    (period === 1 || period === 2) && <ButtonGroup>
                                        { ((investTime.lpTime >= investTime.currentTime && ((Number(fund.hard) > Number(fund.raised) && (!isOwner || !isMember)) || isOwner)) || (period === 1 && (isOwner || isMember))) && <div className='vertical-line'></div> }
                                        {
                                            period === 1  && (
                                                <>
                                                    {/* <RaiseGps show={raiseGp} closeRaise={()=>closeGPRaise()} fund={fund} getVenturePeriod={getVenturePeriod}/> */}
                                                    <RaiseLps show={raiseLp} closeRaise={()=>closeLPRaise()} fund={fund} getVenturePeriod={getVenturePeriod}/>
                                                    { isOwner && <NButton className='alert-light' onClick={() => clickRaise()}>Raise</NButton> }
                                                </>
                                            )
                                        }
                                        
                                        {
                                            isOwner && period === 2 && investTime.lpTime && investTime.lpTime < investTime.currentTime && Number(fund.raised) < Number(fund.goal)
                                            && <>
                                                <div className='vertical-line'></div>
                                                <NButton className='alert-primary long' onClick={() => clickCloseVenture()}>{t('venture.CloseVenture')}</NButton>
                                            </>
                                        }
                                        {
                                            fund.hard && Number(fund.hard) > Number(fund.raised) && ((period === 1 && isMember) || (period === 2 && investTime.lpTime >= investTime.currentTime)) && <>
                                                <Deposit 
                                                    show={deposit} 
                                                    closeDeposit={()=>closeDeposit()} 
                                                    showContent={true} 
                                                    fund={fund} 
                                                    mintToken={mintToken}
                                                    period={period}
                                                    refresh={refreshData}
                                                />
                                                <NButton className='alert-primary' onClick={() => clickDeposit()}>{t('venture.Deposit')}</NButton>
                                            </>
                                        }
                                    </ButtonGroup>
                                }
                                {
                                    isOwner && period === 2 && ((investTime.lpTime && investTime.lpTime <= investTime.currentTime && Number(fund.raised)>= Number(fund.goal)) || Number(fund.raised) === Number(fund.hard) ) &&
                                    <ButtonGroup>
                                        <div className='vertical-line'></div>
                                        <NButton className='alert-primary' onClick={() => clickFreeze()}>{t('venture.Freeze')}</NButton>
                                    </ButtonGroup>
                                }

                                {
                                    period === 3 && <ButtonGroup>
                                        <div className='vertical-line normal'></div>
                                        <NButton className='warn-primary' onClick={() => clickRefund()}>{t('venture.Refund')}</NButton>
                                    </ButtonGroup>
                                }
                                
                                {
                                    period === 5 && !isOwner && !isMember && <ButtonGroup>
                                        <div className='vertical-line normal'></div>
                                        <Withdraw show={withdraw} closeWithdraw={()=>closeWithdraw()} mintToken={mintToken} fund={fund} refresh={refreshData} />
                                        <NButton className='warn-primary' onClick={() => clickWithdraw()}>{t('venture.Withdraw')}</NButton>
                                    </ButtonGroup>
                                }
                               
                                <ConfirmBox show={confirmShow} handleClose={()=>CloseShow()} tips={confirmTips} getVenturePeriod={getVenturePeriod}/>
                            </>
                        </VenturePeriodPanel>
                    }
                </Section>
                <Section>
                    <SectionBlock>
                        <Block>
                            <div className="titleTop">{t('about.TotalAssests')}</div>
                            <Ul className='font-light'>
                                {(period === 4 || period === 5) && <LI><Assests tokenList={ tokenList } /></LI> }
                                <LI>
                                    <div className='title'>{t('venture.RaisedFund')}</div>
                                    <div className='value1'>{fund.raisedDisplay}</div>
                                    <div className='symbol'>{fund.symbol}</div>
                                </LI>
                                {
                                     (period !== 4 && period !== 5) && <LI>
                                        <div className='title'>{t('venture.Goal')}</div>
                                        <div className='value1'>{fund.goalDisplay}</div>
                                        <div className='symbol'>{fund.symbol}</div>
                                    </LI>
                                }
                            </Ul>
                        </Block>
                        <Block className='no-style'>
                            <div className="titleTop">{t('venture.TitleFees')}</div>
                            <Ul className='font-light'>
                                <LI>
                                    <div className='title'>{t('venture.ManageFee')}</div>
                                    <PercentUI val={`${manageFee}%`} />
                                </LI>
                                <LI>
                                    <div className='title'>{t('venture.GPBouns')}</div>
                                    <PercentUI val={`${bonus}%`} />
                                </LI>
                            </Ul>
                        </Block>
                    </SectionBlock>
                </Section>
                    
                <Section>
                    <SectionBlock>
                        <Block>
                            <div className="titleTop">{t('venture.Invest')}</div>
                            <Ul className='font-light'>
                                <LI>
                                    <div className='title'>{t('venture.Mint')}</div>
                                    <div className='value1'>{mintToken.balance}</div>
                                    <div className='symbol'>{mintToken.symbol}</div>
                                </LI>
                                <LI>
                                    <div className='title'>{t('venture.Fund')}</div>
                                    {fund.address && <div className='value1'>{fund.address.slice(0, 6)}...{fund.address.slice(-6)}</div>}
                                    <div className='symbol'>{fund.symbol}</div>
                                </LI>
                                
                            </Ul>
                        </Block>
                        <YourInvest mintToken={mintToken} deposit={fund}></YourInvest>
                    </SectionBlock>
                </Section>
                <Section>
                    {/* <BuyModal show={buyShow} CloseTokenBuy={CloseTokenBuy} />
                    <div className="titleTop">Investment</div>
                    <InvestSection className='font-light'>
                        <div>
                            <div className="input-title">Token contract</div>
                            <div className='inputBrdr'>
                                <Input
                                    className='gray-input'
                                    placeholder="Please enter"
                                    value={tokenInfo.contract}
                                    name='contract'
                                    checked={tokenInfo.contract}
                                    onChange={handleInput}
                                    autoComplete="off"/>
                            </div>
                        </div>
                        <div>
                            <div className="input-title">Token id</div>
                            <div className='inputBrdr'>
                                <Input
                                    className='gray-input'
                                    placeholder="Please enter"
                                    value={tokenInfo.id}
                                    name='id'
                                    checked={tokenInfo.id}
                                    onChange={handleInput}
                                    autoComplete="off"/>
                            </div>
                        </div>
                   
                        <div className='btn-search '>
                            <NButton className='light' onClick={() => clickSearch()}>
                                <img src={Search} alt="" />
                                Search
                            </NButton>
                        </div>
                    </InvestSection> */}
                </Section>
                {
                    (period === 4 || period === 5) && <ProjectList id={id} venturecontract={venturecontract} isOwner={isOwner} fundSymbol={fund.symbol} />
                }
                <Section>
                    {
                        (period === 4 || period === 5) && <NFTlist id={id}></NFTlist>
                    }
                </Section>
            </div>
        </div>
    )
}
