import React, { useState, forwardRef, useImperativeHandle, useEffect } from 'react'
import { Input, Select} from 'antd';
import { useTranslation } from 'react-i18next';
import { ethers } from 'ethers'
import { ManaeFeeToken } from './styles'
import { useWeb3 } from '../../../api/contracts';
import api from "../../../api";
import {getShortDisplay} from '../../../utils/publicJs';

const ManageFeeTransfer = React.memo(forwardRef((props, ref) => {
    let { t } = useTranslation();

    const { state } = useWeb3();
    const { web3Instance, daoContractAddress } = state;

    const [manageFee, setManageFee] = useState();
    const [to_address, setto_address] = useState();

    const [tokenAmount, setTokenAmount] = useState()
    const [tokenAmountValue, setTokenAmountValue] = useState()


    useEffect(
        () => {
            if (!props.data) return
            if (props.data.toAddress) {
                setto_address(props.data.toAddress)
            }
            
        },
        [props.data]
    )
    useEffect(
        () => {
            if (!props.data || !manageFee) return
            if (props.data.tokenAmount) {
                setTokenAmountValue(Number(ethers.utils.formatUnits(props.data.tokenAmount, manageFee.deci)))
                setTokenAmount(props.data.tokenAmount)
            }
        },
        [props.data, manageFee]
    )

    const handleTokenAmount = async (e) => {
        setTokenAmountValue(e.target.value)
        if (manageFee) setTokenAmount(ethers.utils.parseUnits(String(e.target.value), manageFee.deci))
    }

    const handleInputChange = (e) => {
        const { name, value } = e.target
        switch(name) {
            case 'to_address':
                setto_address(value)
                break
            case 'valueAmount':
                setTokenAmount(value)
                break
            default:
                return
        }
    }

    const getToken = async (contract, tokenAddress)=>{
        try {
            const mockERC20 = await api.erc20.getContract(web3Instance, tokenAddress)

            const symbol = await mockERC20.symbol()
            const deci = await mockERC20.decimals() 
            let balance = await api.vault.getTokenBalance(contract, tokenAddress)
            balance = ethers.utils.formatUnits(balance, deci)

            return { symbol, deci, balance: Number(balance), balanceDisplay:getShortDisplay(balance), address:tokenAddress }
        } catch (error) {
            console.error(error)
            return {}
        }
    }

    const getManageFee = async()=>{
        try {
            const contract = await api.vault.getManageFeeContract(web3Instance, daoContractAddress.ManageFeeVaultManager)
            const tokenlist = await api.vault.getTokenList(contract)
            if (!tokenlist || tokenlist.length === 0) return
            const token = await getToken(contract, tokenlist[0])
            setManageFee(token)
        } catch (error) {
            console.error('getManageFee failed', error)
        }
    }

    useEffect(()=>{
        if (!daoContractAddress || !daoContractAddress.ManageFeeVaultManager) return
        getManageFee()
    }, [daoContractAddress])


    useImperativeHandle(ref, () => ({
        checkInvalid() {
            if (!to_address || !ethers.utils.isAddress(to_address)) {
                return 'vote.NewVoteAlertInvalidAddress'
            }
            console.log(tokenAmountValue, manageFee.balance)
            if (!tokenAmount || !Number(tokenAmountValue) || Number(tokenAmountValue) > Number(manageFee && manageFee.balance)) {
                return 'vote.NewVoteAlertTokenAmount'
            }
        },
        tokenDeci: manageFee && manageFee.deci,
        data: {
            tokenAmount: tokenAmount,
            tokenAddress: manageFee && manageFee.address,
            toAddress: to_address
        }
    }));

    return (
        <>
        <div className="venture-line">
            <div className="inputBrdr">
                {
                    manageFee && <ManaeFeeToken value={manageFee.address} key={manageFee.address}>
                        <div style={{ display:'flex', alignItems:'center', lineHeight: '4.4rem'}}>
                            <div style={{ display:'flex', flexDirection:'row', flexGrow:1}}>
                                <div style={{color: '#10164B', textTransform:'uppercase', marginRight: '1rem'}}>{manageFee.symbol}</div>
                                <div style={{color: '#A6A6B7'}}>{`${manageFee.address.slice(0,5)}${manageFee.address.slice(-5)}`}</div>
                            </div>
                            <div style={{textAlign:'right', color: '#A6A6B7', paddingRight: '2rem'}}>{t('vote.Available')}: {manageFee.balanceDisplay}</div>
                        </div>
                    </ManaeFeeToken>
                }
            </div>
        </div>
        <div className='venture-line'>
            <div className="line">
                <div className='label'>{t('vote.newOwner')}</div>
            </div>
            <div className='inputBrdr'>
                <Input
                    placeholder={t('vote.enter')}
                    value={to_address}
                    name='to_address'
                    onChange={handleInputChange}
                    autoComplete="off"/>
            </div>
        </div>
        <div className='venture-line'>
            <div className="line">
             <div className='label'>{t('vote.fillAmount')}</div>
            </div>
            <div className='inputBrdr'>
                <Input
                    placeholder={t('vote.enter')}
                    value={tokenAmountValue}
                    name='valueAmount'
                    type="number"
                    onChange={handleTokenAmount}
                    autoComplete="off"/>
            </div>
        </div>
        </>
    )
}))

export default ManageFeeTransfer