import React, { useState, forwardRef, useImperativeHandle, useEffect } from 'react'
import { DatePicker, Select, Input } from 'antd'
import { useTranslation } from 'react-i18next';
import moment from 'moment';
import { useWeb3 } from '../../../api/contracts';
import Estimate from '../../ventureModal/estimate';
import { SelectShortBrdr } from './styles'
import { ethers } from "ethers"
import TokenSelect from "./tokenSelect"

const ApproveGP = React.memo(forwardRef((props, ref)=>{
    const { gplist, tokenlist, data } = props

    const { t } = useTranslation();

    const { state } = useWeb3();
    const { vaultcontract, web3Instance } = state;
    
    const [approveDuration, setApproveDuration] = useState()
    const [timeValue, setTimeValue] = useState()
    const [selectGP, setSelectGP] = useState()

    const [selectToken, setSelectToken] = useState({})
    const [tokenAmount, setTokenAmount] = useState()

    useEffect(() => {
        if (data.limit && data.limit.address) {
            const tk = tokenlist.find(t => t.address === data.limit.address)
            setSelectToken(tk)
        }
        if (data.limit && data.limit.amountValue) {
            setTokenAmount(data.limit.amountValue)
        }
        if (data.timeValue) {
            setTimeValue(data.timeValue)
            const dateTime = Date.parse(data.timeValue._d)
            setApproveDuration(dateTime/1000)
        }
        if (data.gpAddress) {
            setSelectGP(data.gpAddress)
        }
        
    }, [data])


    const disabledDate = (current) => {
        return current && current <= moment().startOf('day');
    }
    const handleApproveDuration = v => {
        setTimeValue(v)
        const dateTime = Date.parse(v._d)
        setApproveDuration(dateTime/1000)
    }

    const handleInputChangeAmount = async (amount) => {
        setTokenAmount(amount)
    }

    const handleSelect = async(value) => {
        const tk = tokenlist.find(t => t.address === value)
        setSelectToken(tk)
    }

    useImperativeHandle(ref, () => ({
        checkInvalid() {
            if (!selectGP || !approveDuration) {
                return 'vote.NewVoteError' 
            }
            if (tokenAmount <= 0) {
                return 'vote.NewVoteAlertTokenAmount'
            }
            if (!selectToken.address) {
                return 'vote.NewVoteAlertInvalidAddress'
            }
            if (Number(selectToken.balanceDisplay) < tokenAmount) {
                return 'vote.NewVoteAlertTokenAmount'
            }
        },
        data: {
            gpAddress: selectGP,
            callee: vaultcontract && vaultcontract.address,
            limit: {
                address: selectToken.address,
                amount: tokenAmount ? ethers.utils.parseUnits(tokenAmount, selectToken.deci) : 0,
                amountValue: tokenAmount
            },
            approvTime: approveDuration,
            timeValue,
        }
    }));

    return <>
        <div className='venture-line'>
            <div className="line">
                <div className='label'>{t('vote.Duration')}</div>
            </div>
            <DatePicker
                value={timeValue}
                className="myDatePicker"
                placeholder={t('vote.Duration')}
                style={{ width: '100%' }}
                format="YYYY-MM-DD HH:mm:ss"
                allowClear={false}
                disabledDate={disabledDate}
                onChange={handleApproveDuration}
                showTime={true}
                showNow={false} />
        </div>
        <div className='venture-line'>
            <div className="line">
                <div className='label'>{t('vote.ApproveGp')}</div>
            </div>
            <SelectShortBrdr onChange={(v) => { setSelectGP(v) }} defaultValue={selectGP} key={selectGP}>
                {
                    gplist.map((item, i)=>{
                        return (
                            <Select.Option value={item.address} key={i}>
                                <div className='select-item'>{item.address} </div>
                            </Select.Option>
                        )
                    })
                }
            </SelectShortBrdr>
        </div>
        <div className='venture-line'>
            <div className="line">
                <div className='label'>{t('vote.ApproveLimit')}</div>
            </div>
            <TokenSelect list={tokenlist} handleSelect={handleSelect} t={t} defaultValue={data.limit && data.limit.address} />
        </div>
        <div className='venture-line'>
            <div className="line">
                <div className='label'>{t('vote.ApproveLimitAmount')}</div>
            </div>
            <div className='inputBrdr'>
                <Input
                    type="number"
                    value={tokenAmount}
                    placeholder={t('vote.enter')}
                    onChange={(e) => handleInputChangeAmount(e.target.value)}
                    autoComplete="off"/>
                <span className='suffix'>{selectToken.symbol}</span>
            </div>
        </div>
    </>
}))

export default ApproveGP