import React from 'react'
import { Select } from 'antd'
import { SelectShortBrdr } from './styles'

const TokenSelect = React.memo((props)=>{
    const { list, handleSelect, t } = props
    return (
        <div className="inputBrdr">
            <SelectShortBrdr style={{ width: '100%' }} onChange={handleSelect} placeholder={t('vote.SelectOption')} defaultValue={ props.defaultValue }>
                {
                    list.map((i) =>
                        <Select.Option value={i.address} key={i.address}>
                            <div style={{ display:'flex', alignItems:'center', lineHeight: '4.4rem'}}>
                                <div style={{ display:'flex', flexDirection:'row', flexGrow:1}}>
                                    <div style={{color: '#10164B', textTransform:'uppercase', marginRight: '1rem'}}>{i.symbol}</div>
                                    <div style={{color: '#A6A6B7'}}>{`${i.address.slice(0,5)}${i.address.slice(-5)}`}</div>
                                </div>
                                <div style={{textAlign:'right', color: '#A6A6B7', paddingRight: '2rem'}}>{t('vote.Available')}e: {i.balanceDisplay}</div>
                            </div>
                        </Select.Option>
                    )
                }
            </SelectShortBrdr>
        </div>
    )
})
export default TokenSelect