import React, { useState, forwardRef, useImperativeHandle, useRef, useEffect } from 'react'
import { Input } from 'antd'
import { useTranslation } from 'react-i18next';
import { GpInfo } from './styles'
import publicJs from '../../../utils/publicJs';
import VentureVoteConfig from '../../create/ventureVoteConfig'
import { ethers } from 'ethers';

const AddGP = React.memo(forwardRef((props, ref)=>{
    const { gplist, updateGplist, data } = props
    const { t } = useTranslation();

    const voteRef = useRef();

    const [newGp, setNewGp] = useState()

    const handleSetBonus = (e, index)=>{
        let newArray = [...gplist];
        const { name, value } = e.target;
        newArray[index].bonus = Number(value);
        updateGplist(newArray)
    }

    useEffect(
        () => {
            console.log(data)
            const newgp = {}
            if (data.newGP) {
                newgp.address = data.newGP
            }
            if (data.gpList && data.gpList.length > 0) {
                const lst = []
                for (const i in data.gpList) {
                    if (newgp.address === data.gpList[i]) {
                        newgp.bonus = data.gpBonus[i] && (data.gpBonus[i] / 100)
                    } else {
                        lst.push({
                            address: data.gpList[i],
                            bonus: data.gpBonus[i] && (data.gpBonus[i] / 100)
                        })
                    }
                }
                updateGplist(lst)
            }
            if (data.newGP) {
                setNewGp(newgp)
            }
          
        },
        [data]
    )


    useImperativeHandle(ref, () => ({
        checkInvalid() {
            if (!newGp || !newGp.address || !ethers.utils.isAddress(newGp.address)) {
                return 'vote.NewVoteAlertInvalidAddress'
            }
            if (!voteRef.current || voteRef.current.checkInvalid()) {
                return 'vote.NewVoteError'
            }
        },
        getData() {
            const gp_address = gplist.map(l=>l.address)
            const gp_bonus = gplist.map(l=>l.bonus*100)
            gp_address.push(newGp.address)
            gp_bonus.push(newGp.bonus*100)
            return {
                newGP: newGp.address,
                gpList: gp_address, 
                gpBonus: gp_bonus, 
                newVoteConfig: voteRef.current && voteRef.current.newVoteConfig
            }
        }
    }));

    return (
        <div className='venture-line'>
            <div className="line">
                <div className='label'>{t('vote.GPAddress')}</div>
            </div>
            <div className='inputBrdr rht'>
                <Input
                    value={newGp && newGp.address}
                    autoComplete="off"
                    onChange={e=>{setNewGp({...newGp, address:e.target.value})}}
                />
            </div>
            {
                newGp && newGp.address && <>
                    <GpInfo>
                        <div className="line top">
                            <div className='label'>{t('vote.GpInfo')}</div>
                        </div>
                        <ul>
                            <li>
                                <div className="line">
                                    <div className="label lft"><span>{t('vote.GPInfoAddress')}</span>{publicJs.AddresstoShow(newGp.address)}</div>
                                    <div className='inputBrdr rht'>
                                        <div className="label">{t('vote.bonus')}</div>
                                        <Input
                                            type="number"
                                            value={ newGp && newGp.bonus }
                                            placeholder={t('vote.bonus')}
                                            onChange={(e) => setNewGp({...newGp, bonus:Number(e.target.value)})}
                                            autoComplete="off"/>
                                        <span className='suffix'>%</span>
                                    </div>
                                </div>
                            </li>
                            {
                                gplist.map((item, i)=>{
                                    return <li key={i}>
                                                <div className="line">
                                                    <div className="label lft"> <span>{t('vote.GPInfoAddress')}</span>{publicJs.AddresstoShow(item.address)}
            
                                                    </div>
            
                                                    <div className='inputBrdr rht'>
                                                        <div className="label">{t('vote.bonus')}</div>
                                                        <Input
                                                            type="number"
                                                            value={ item.bonus }
                                                            placeholder={t('vote.bonus')}
                                                            onChange={(event) => handleSetBonus(event, i)}
                                                            autoComplete="off"/>
                                                        <span className='suffix'>%</span>
                                                    </div>
                                                </div>
                                            </li>
                                })

                            }
                        </ul>
                    </GpInfo>
                    <VentureVoteConfig ref={voteRef} gp={gplist.length + 1} titleClass='label' minSupportNumber={ data.newVoteConfig && data.newVoteConfig.minSurportRequire } />
                </>
            }
            
        </div>
    )
}))

export default AddGP