import React, { useState, useEffect } from 'react'
import { useTranslation } from "react-i18next";
import styled from 'styled-components';
import { getInvestProjects } from '../../api/apiHTTP'


const Ul = styled.ul`
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 2rem;
    &.project-ul {
        margin-bottom: 0;
    }
`;


const ProjectLi = styled.li`
    margin-bottom: 3rem;
    width: 44.1rem;
    height: 14rem;
    background: #FFFFFF;
    display: flex;
    flex-direction: row;
    padding: 2.7rem 1.5rem 2.3rem 2.7rem;
    border-radius: 2.4rem;
    box-sizing: border-box;
    box-shadow: 0px 0px 10px 0px rgba(16, 22, 75, 0.05);
    margin-right: 5rem;
    position: relative;
    :nth-child(3n){
		margin-right: 0;
	}
    &.occupy-add {
        cursor: pointer;
    }
`

const ProjectImg = styled.div`
    width: 8rem;
    height:8rem;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 2.6rem;
    &.occupy-left{
        background: #F4F5FB;
        position: relative;
        img {
            width: 3rem;
            height: 3rem;
            position: absolute;
            top: 50%;
            left: 50%;
            margin-left: -1.5rem;
            margin-top: -1.5rem;
        }
    }
    img {
        width: 100%;
        height: 100%;
    }
`

const ProjectContent = styled.div`
    color: #10164B;
    font-weight: 300;
    flex: 1;
        
    .title {
        height: 3.8rem;
        font-size: 2.4rem;
        font-weight: 300;
        line-height: 2.8rem;
    }
    .desc {
        font-size: 1.8rem;
        line-height: 1.8rem;
        word-break: break-all;
        text-overflow: ellipsis;
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
    }
    &.occupy-text {
        font-size: 2.4rem;
        color: #B0B2C0;
        line-height: 8rem;
    }
`
const ProjectAmount = styled.span`
    position: absolute;
    right: 2rem;
    top: 2rem;
`

const Section = styled.section`
    margin-top: 4rem;
    .titleTop {
        font-size: 2.4rem;
        font-weight: 400;
        color: #10164B;
        line-height: 2.8rem;
        margin: 0;
        margin-bottom: 2rem;
    }
`


const ProjectList = (props) => {
    const { t } = useTranslation();
    const [projectList, setProjectList] = useState([])

    const getProjects = async () => {
        try {
            const result = await getInvestProjects(props.venturecontract.address, 0, 10000)
            if (!result || !result.data) throw Error(result.error)

            const data = result.data.data
            const proj = data.projects
            const plist = proj.data.map(l => {
                return {
                    name: l.name,
                    desc: l.desc,
                    homepage: l.homepage,
                    amount: l.amount,
                    img: `https://cloudflare-ipfs.com/ipfs/${l.logo}`
                }
            })
            setProjectList(plist)
        } catch (error) {
            console.error('getInvestProjects failed', error)
        }
    }

    useEffect(() => {
        if (!props.id || !props.venturecontract) return
        getProjects()
    }, [props.id, props.venturecontract])

    if (projectList.length === 0) return <></>


    return (
        <Section>
            <div className="titleTop">{t('venture.Investment')}</div>
            <Ul className='project-ul flex-lst font-light'>
                {
                    projectList.map((item, i) => {
                        return (
                            <ProjectLi key={i}>
                                <ProjectImg>
                                    <img src={item.img} alt="" />
                                </ProjectImg>
                                <ProjectContent>
                                    <div className='title'>{item.name}</div>
                                    <div className='desc'>{item.desc}</div>
                                </ProjectContent>
                                <ProjectAmount>{item.amount} {props.fundSymbol}</ProjectAmount>
                            </ProjectLi>
                        )
                    })
                }
            </Ul>

        </Section>
    )
}

export default ProjectList