const ManagementItem = (props) => {
    const { t } = useTranslation();
    const { data: item, period, i, isMember } = props
    
    const clickFunc = () => {
        if (!disabled) {
            props.clickFunc(i, period, isMember)
        }
    }
    const disabled = useMemo(
        () => {
            if (i === 8) {
                // withdraw
                return period !== 6
            } else if (i === 6 || i === 7) { 
                return period !== 5
            } else {
                return false
            }
        },
        [period, i]
    )
    return (
        <li key={item.name} className={`${item.className} ${disabled ? 'disabled' : 'show'}`} onClick={ ()=> clickFunc() }>
            <div className='wrapper'>
                <div>
                    <ReactSVG src={item.img} className="svg" />
                </div>
                <div>{t(item.name)}</div>
            </div>
        </li>
    )
}

export default function VentureManagement(props) {
    const { t } = useTranslation();

    const { state, dispatch } = useWeb3();
    const { maincontract, whoAmI, venturecontract, web3Instance, stockcontract, daoContractAddress, Account } = state;

    const [id, setAId] = useState(null);

    const [isMember, setisMember] = useState(false);
    const [isModerator, setisModerator] = useState(false);
    const [isOwner, setisOwner] = useState(false);

    const [withdraw, setWithdraw] = useState(false);
    const [mintToken, setMintToken] = useState({})
    const [fund, setFund] = useState({
        address: '',
    })

    const [period, setPeriod] = useState()
    const [swapShow, setswapShow] = useState(false);


    useEffect(() => {
        setAId(props.match.params.id);
    }, [props.match.params.id]);

    useEffect(async () => {
        if (maincontract == null) dispatch({ type: 'LOAD_MAINCONTRACT' });
    }, []);


    useEffect(() => {
        if (whoAmI == null) return;
        setisOwner(whoAmI[0]);
        setisModerator(whoAmI[1]);
        setisMember(whoAmI[2]);
    }, [id, maincontract, whoAmI]);

    useEffect(() => {
        if (!venturecontract) return
        const getfund = async () => {
            try {
                const { raised, goal, address } = await api.venture.getFundInfo(venturecontract)
                const tokenContract = await api.erc20.getContract(web3Instance, address)
                const symbol = await tokenContract.symbol()
                setFund({
                    raised, goal, symbol, address
                })
            } catch (error) {
                console.error("getfund failed", error)
            }

        }
        getfund()
    }, [venturecontract])

    const getVenturePeriod = async (contract) => {
        try {
            const period = await contract.getVenturePeriod()
            setPeriod(period + 1)
        } catch (error) {
            console.error('getVenturePeriod failed', error)
        }
    }

    useEffect(() => {
        if (!venturecontract) return
        getVenturePeriod(venturecontract)
    }, [venturecontract])


    const updateDaoID = useCallback(
        id => setAId(id),
        []
    )

    

    const getStockToken = async()=>{
        const deci = await stockcontract.decimals()
        let mintToken = {
            deci: deci,
            address: daoContractAddress['VenturesStockManager'],
            symbol: await stockcontract.symbol(),
            balance: ethers.utils.formatUnits(await stockcontract.totalSupply(), deci),
            userBalance: ethers.utils.formatUnits(await stockcontract.balanceOf(Account), deci)
        }
        mintToken.userBalanceDisplay = getShortDisplay( mintToken.userBalance)
        mintToken.balanceDisplay = getShortDisplay( mintToken.balance)
        setMintToken(mintToken)
    }

 
    const closeWithdraw = ()=>{
        setWithdraw(false)
    }

    const clickFunc = useCallback(
        (i, period, isMember) => {
            if (!isMember) {
                dispatch({ type: 'MSGTYPE', payload: { msg: `${t('venture.ManagementAuth')}`, closable: true } });
                return
            }
            if (i !== 8) {
                if (i === 6 || i === 7) {
                    if (period !== 5) {
                        dispatch({ type: 'MSGTYPE', payload: { msg: `${t('venture.ManagementTip')}`, closable: true } });
                        return
                    }
                }
                if (i === 0) {
                    setswapShow(true)
                    return
                }
                if (i === 4) {
                    dispatch({ type: 'MSGTYPE', payload: { msg: `${t('venture.NFTTip')}`, closable: true } });
                    return
                }
                if (list[i] && list[i].voteKey) {
                    props.history.push(`/newVote/${props.match.params.handle}?url=${props.match.url}&venture=1&type=${list[i].voteKey-1}`)
                }
            } else {
                if (period !== 6) {
                    dispatch({ type: 'MSGTYPE', payload: { msg: `${t('venture.ManagementTip')}`, closable: true } });
                    return
                }
                setWithdraw(true);
            }
        },
        []
    )


    return (
        <div>
            <div className='container'>
                <Left history={props.history} handle={props.match.params.handle} updateDaoID={updateDaoID} />
                <TokenBuy show={swapShow} closeTokenBuy={()=>setswapShow(false)} venturecontract={venturecontract} fund={fund} />
                <Section>
                    <ul>
                        {
                            list.map((item, i) => (
                                <ManagementItem
                                    key={item.name}
                                    i={i}
                                    className={item.className}
                                    data={item}
                                    period={period}
                                    isMember={isMember}
                                    clickFunc={clickFunc}
                                />
                            ))
                        }

                    </ul>
                </Section>
                {(isOwner && period === 5) && <ProjectList id={props.match.params.id} venturecontract={venturecontract} fundSymbol={fund.symbol}></ProjectList>}
                <Withdraw show={withdraw} closeWithdraw={()=>closeWithdraw()} mintToken={mintToken} fund={fund} />
            </div>
        </div>
    )
}
