import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { useTranslation } from "react-i18next";
import { ethers } from 'ethers'
import { useWeb3 } from "../../api/contracts";
import api from '../../api/index';
import { getShortDisplay } from '../../utils/publicJs';

const Assest = styled.div`
    &:hover {
        background: #FFEFF7;
    }
    .asset-error {
        margin-left: 1rem;
    }
`

const Assets = React.memo(props => {
    const { t } = useTranslation();

    const { state } = useWeb3();
    const { Account, venturecontract, web3Instance } = state;

    const [assets, setAssets] = useState({});
    const [errorTokens, setErrorTokens] = useState(null)

    const getAllAssets = async (tokenlist) => {
        const fundAddress = await venturecontract.TokenType()
        const fundContract = await api.erc20.getContract(web3Instance, fundAddress)
        const symbol = await fundContract.symbol()
        const deci = await fundContract.decimals()
        let total_value = 0
        const errors = []
        for (const token of tokenlist) {
            if (token.address === fundAddress) {
                total_value += Number(token.balance)
                continue
            }
            try {
                const contract = await api.erc20.getContract(web3Instance, token.address)
                const num = await api.venture.getAmountOutMin(web3Instance, web3Instance.getSigner(Account), {
                    tokenIn: token.address,
                    tokenOut: fundAddress,
                    amountIn: ethers.utils.parseUnits(String(token.balance), await contract.decimals())
                })

                total_value += Number(ethers.utils.formatUnits(num, deci))
            } catch (error) {
                errors.push(token.symbol)
            }
        }
        return {
            errors: errors.slice(0, 5).join(', ') + (errors.length > 5 ? '...' : ''),
            assets: { total: getShortDisplay(total_value), symbol },
        }
    }

    useEffect(
        async () => {
            if (!venturecontract || !props.tokenList || props.tokenList.length === 0) return
            try {
                const {assets, errors} = await getAllAssets(props.tokenList)
                setAssets(assets)
                if (errors.length > 0) setErrorTokens(errors)
            } catch (error) {
                console.error('get getAllAssets failed', error)
            }
        },
        [props.tokenList, venturecontract]
    )

    return <Assest>
        <div className='title'>{t('about.TotalAssests')}</div>
        <div className="value1">{assets.total}</div>
        <div className="symbol">
            {assets.symbol}
            {
                errorTokens && <span className='asset-error'>
                    {errorTokens} {t('about.AssetsValue')}
                </span>
            }
        </div>
        
    </Assest>
})

export default Assets