import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { useWeb3 } from "../../api/contracts";
import LoadingNew from "../loadingNEW";
import NTFnull from '../../img/NTFnull.png'


const NTFli = styled.li`
    margin-bottom: 4rem;
    width: 32.8rem;
    /* height: 44.8rem; */
    background: #FFFFFF;
    box-shadow: 0px 0px 4px 2px rgba(0, 0, 0, 0.06);
    border-radius: 1rem;
    padding: 1.9rem;
    box-sizing: border-box;
    img {
        /* width: 100%; */
        width: 29rem;
        height: 29rem;
        margin-bottom: 2rem;
    }
`

const NTFContent = styled.div`
    .name {
        font-size: 2rem;
        font-weight: 400;
        line-height: 2.4rem;
    }
    .desc {
        width: 100%;
        /* height: 3.8rem; */
        font-size: 1.6rem;
        font-weight: 300;
        line-height: 1.9rem;
        word-break: break-all;
        text-overflow: ellipsis;
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        margin-top: 1.5rem;
        margin-bottom: 2rem;
    }
    .price {
        font-size: 2rem;
        font-weight: 400;
        line-height: 2.4rem;
    }
`
const Ul = styled.ul`
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 2rem;
    &.project-ul {
        margin-bottom: 0;
    }
`;

const NTFContainer = styled.div`
    img {
        width: 100%;
    }
`


const NFTlist = (props) => {
    let ntf = {
        img: '',
        name: 'Chimera by mpkoz',
        desc: 'Chimera is a mutation,its genetics a merging of past and present.',
        price: 1111
    }
    const { state } = useWeb3();
    const { venturecontract } = state;

    const [ntfList, setNtfList] = useState(new Array(8).fill(ntf))
    const [nftLoading, setNftLoading] = useState(true)

    const getNfts = async () => {
        try {
            const nlist = []
            setNtfList(nlist)
            setNftLoading(false)
        } catch (error) {
            console.error('getNfts failed', error)
        }
    }

    useEffect(() => {
        getNfts()
    }, [props.id, venturecontract])

    if (nftLoading) return <></>

    return (
        <NTFContainer>
            <div className="titleTop">Investment NFT</div>
            {
                ntfList.length === 0
                    ? <img className='ntf-null' src={NTFnull} alt="" />
                    : (
                        <Ul className='flex-lst font-light'>
                            {
                                nftLoading ? (<LoadingNew></LoadingNew>) : ntfList.map((item, i) => {
                                    return (
                                        <NTFli key={i}>
                                            <img src={item.img} alt="" />
                                            <NTFContent>
                                                <div className='name'>{item.name}</div>
                                                <div className='desc'>{item.desc}</div>
                                                <div className='price'>{item.price}</div>
                                            </NTFContent>
                                        </NTFli>
                                    )
                                })
                            }
                        </Ul>
                    )
            }

        </NTFContainer>
    )
}
export default NFTlist